<style>
  .store-check-wraper {
    border: 1px solid #ececec;
    border-radius: 8px;
    padding: 8px;
    width: 530px;
    box-sizing: border-box;
  }

  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
</style>

<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="140px">
        <el-form-item label="活动类型">
          <p style="color: #222;font-size: 16px; font-weight: bold;">{{addParamMesg.activityTypeName}}</p>
        </el-form-item>
        <el-form-item label="选择区域" required>
          {{addParamMesg.regionName}}
        </el-form-item>
        <el-form-item label="活动编号">
          {{addParamMesg.activityCode}}
        </el-form-item>
        <el-form-item label="活动名称">
          {{addParamMesg.activityName}}
        </el-form-item>
        <el-form-item label="开始时间">
          {{addParamMesg.activityStartDate}}
        </el-form-item>
        <el-form-item label="结束时间">
          {{addParamMesg.activityEndDate}}
        </el-form-item>
        <el-form-item label="参加活动人" v-if="addParamMesg.shopType == 2"> 
          {{addParamMesg.isMemberName}}
        </el-form-item>
        <el-form-item label="参与门店">
          <!-- 选择参与门店  线上 -->
          <div v-if="addParamMesg.shopType == 1 && activityStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
              :data="activityStoresCheckedList"
              max-height="300"
              size="small">
              <el-table-column min-width="226px" label="门店编码/名称">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100" prop="saleTypeName" label="门店类型"></el-table-column>
            </el-table>
          </div>
          <!-- 选择参与门店  线下 -->
          <div v-if="addParamMesg.shopType == 2 && offlineStoresCheckedList.length > 0" class="store-check-wraper">
            <el-table
              class="fixed-height"
              :data="activityStoresCheckedList"
              max-height="300"
              size="small">
              <el-table-column min-width="226px" label="门店编码/名称">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100" prop="saleTypeName" label="门店类型"></el-table-column>
            </el-table>
          </div>
        </el-form-item>      
        <el-form-item label="参与商品">
          <!-- 选择参与商品  线上 || 线下  -->
          <div v-if="addParamMesg.shopType == 1 ? activityGoodsCheckedList.length > 0 : offlineGoodsCheckedList.length > 0">
            <el-table
              class="fixed-height"
              :data="addParamMesg.shopType == 1 ? activityGoodsCheckedList : offlineGoodsCheckedList"
              empty-text="暂无选中任何商品"
              style="width:100%;"
              max-height="400"
              size="small">
              <!-- 选择参与商品  线上 -->
              <el-table-column min-width="220px" label="商品信息" v-if="addParamMesg.shopType == 1">
                <template slot-scope="scope">
                  <div class="ll-p-m-w">
                    <div class="ll-img-w">
                      <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                      <img v-else src="/shop/static/img_error.png" alt="">
                    </div>
                    <div class="ll-p-m-c-r">
                      <div>
                        {{scope.row.goodsName || '-'}}
                      </div>
                      <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                    </div>
                  </div>
                </template>
              </el-table-column>
              <!-- 选择参与商品  线下 -->
              <el-table-column min-width="170px" label="商品信息" v-if="addParamMesg.shopType == 2">
                <template slot-scope="scope">
                  <div class="ll-p-m-w">
                    <div class="ll-p-m-c-r">
                      <div>
                        {{scope.row.name || '-'}}
                      </div>
                      <div class="ll-erp-p-no">ERP编码：{{scope.row.goodsNo || '--'}}</div>
                    </div>
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
              <!-- 选择参与商品  线上 -->
              <el-table-column min-width="90px" prop="storeMinPrice" label="门店最低价" v-if="addParamMesg.shopType == 1"></el-table-column>
              <el-table-column min-width="150px" label="限购次数(为空不限)">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.limitGoodsCount}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column min-width="110px" label="活动库存">
                <template slot-scope="scope">
                  <div>
                    {{scope.row.curStock}}
                  </div>
                </template>
              </el-table-column>
              <el-table-column
                label="活动规则"
                min-width="120px">
                <template slot-scope="scope" size="small">
                  <div class="set-dis-msg-list-wrap">
                    <div class="set-dis-msg-item" v-for="(item, idx) in scope.row.activityDiscounts" :key="idx">
                      <span>{{item.qty}}</span>
                      <span style="padding: 0 5px; color: #222;">份</span> 
                      <span>{{item.amount}}</span>
                      <span style="padding: 0 5px; color: #222;">元</span>
                    </div>
                  </div>
                </template>
              </el-table-column>
            </el-table>
            <div v-if="addParamMesg.shopType == 1">
              <span style="color: red;">*</span>注意：权重排序，先创建的活动商品在前；剩余的按前端规则走。
            </div>
            <div v-if="addParamMesg.shopType == 2">
              <span style="color: red;">*</span>注意：如遇特殊情况（pos机断网等），可能导致限购次数和活动库存不准。
            </div>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  const reqUrl = {
    // 活动规则编辑跳转
    edit: "/plateform/a/activityRule/edit"
  }
  export default {
    data() {
      return {
        addParamMesg: {
          regionName: '',
          regionCode: '',
          id: '',
          activityId: '', // 活动id
          activityType: '', // 活动类型
          activityTypeName: '', // 活动类型名称
          shopType: '', // 商城类型 ：1： 商城，2：门店
          shopTypeName: '', 
          isMember: 1, // 是否是会员 ：0：全部，1： 是，2：否
          activityName: '', // 活动名称
          activityStartDate: '', // 活动开始时间
          activityEndDate: '', // 活动结束时间
          activityCode: '', // 活动编码
          // 参与门店
          activityStoreList: [],
          // 活动商品集合
          platCityGoodsInfos: []
        },
        // 参与门店选中的  线上
        activityStoresCheckedList: [],
        // 参与的选中的商品  线下
        activityGoodsCheckedList: [],

        // 参与门店选中的 线下
        offlineStoresCheckedList: [],
        // 参与的选中的商品 线下
        offlineGoodsCheckedList: []
      }
    },
    created() {
      let activityId = this.$route.params.activityId
      this.addParamMesg.activityId = activityId
    },
    mounted() {
      this.getDetail()
    },
    methods: {
      /**
       * [getDetail 获取拼团活动详情]
       * @return {[type]} [description]
       */
      getDetail() {
        let activityId = this.addParamMesg.activityId || ''
        utils.showLoading()
        this.$http.post(reqUrl.edit, {activityId: activityId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              let createGoodsMesg = res.data
              this.addParamMesg.id = createGoodsMesg.id
              this.addParamMesg.activityId = createGoodsMesg.id
              this.addParamMesg.regionName = createGoodsMesg.regionName || ''
              this.addParamMesg.regionCode = createGoodsMesg.regionCode || ''
              this.addParamMesg.activityType = createGoodsMesg.activityType || ''
              this.addParamMesg.activityTypeName = createGoodsMesg.activityTypeName || ''
              this.addParamMesg.activityName = createGoodsMesg.activityName || ''
              this.addParamMesg.activityStartDate = createGoodsMesg.activityStartDate || ''
              this.addParamMesg.activityEndDate = createGoodsMesg.activityEndDate || ''
              this.addParamMesg.activityCode = createGoodsMesg.activityCode || ''
              this.addParamMesg.isMember = createGoodsMesg.isMember ? parseInt(createGoodsMesg.isMember) : 0
              if(createGoodsMesg.isMember === '1') {
                this.addParamMesg.isMemberName = '会员'
              }else if(createGoodsMesg.isMember === '2') {
                this.addParamMesg.isMemberName = '非会员'
              }else if(createGoodsMesg.isMember === '0') {
                this.addParamMesg.isMemberName = '全部'
              }
              this.addParamMesg.shopType = createGoodsMesg.shopType || ''
              this.addParamMesg.shopTypeName = createGoodsMesg.shopTypeName || ''

              if(createGoodsMesg.storeVos && createGoodsMesg.storeVos.length) {
                if(createGoodsMesg.shopType === 1) {
                  this.activityStoresCheckedList = createGoodsMesg.storeVos
                }else if(createGoodsMesg.shopType === 2) {
                  for(let item of createGoodsMesg.storeVos) {
                    item.storeNo = item.erpStoreNo
                    item.storeName = item.erpStoreName
                    item.managementTypeCode = item.saleType
                    item.managementTypeValue = item.saleTypeName
                  }
                  this.offlineStoresCheckedList = createGoodsMesg.storeVos
                }
              }
              if(createGoodsMesg.platCityGoodsVos && createGoodsMesg.platCityGoodsVos.length) {
                if(createGoodsMesg.shopType === 1) {
                  this.activityGoodsCheckedList = createGoodsMesg.platCityGoodsVos
                }else if(createGoodsMesg.shopType === 2) {
                  for(let item of createGoodsMesg.platCityGoodsVos) {
                    item.name = item.activityGoodsName
                    item.categoryNoName = item.categoryName
                  }
                  this.offlineGoodsCheckedList = createGoodsMesg.platCityGoodsVos
                }
              }

            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


